@import url(https://fonts.googleapis.com/css2?family=Barlow:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.project_screen {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  position: relative;
}

.project_screen .info {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 20%;
  width: 100%;
}

.about_screen 
{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  position: relative;
}

.about_screen .info 
{
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  text-align: center;
}

.home_img {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Barlow', sans-serif;
}

h4, a
{
  color: black;
  text-decoration: none;
}

ul
{
  list-style-type: none;
}

.btn_about 
{
  padding: 12px 32px; 
  font-size: 1rem;
  text-transform: uppercase;
  background: #00B4DB;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #00B4DB, #0083B0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 25px;
  color: #232526;
  border: 1px solid #fff;
  font-weight: 600; 
  cursor: pointer;
}

.btn_projects 
{
  padding: 12px 32px; 
  font-size: 1rem;
  text-transform: uppercase;
  background-color: #fbb034;
  background-image: linear-gradient(315deg, #fbb034 0%, #ffdd00 74%);
  border-radius: 25px;
  color: #232526;
  border: 1px solid #fff;
  font-weight: 600; 
  cursor: pointer;
}

.btn_projects:hover
{
    background-color: #fbb034;
    background-image: linear-gradient(315deg, #ffdd00 74%, #fbb034 0%,);
    border-radius: 25px;
    color: #fff;
    transition: 0.3s;
}

.btn_about:hover
{
    background: #00B4DB;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #0083B0,  #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    border-radius: 25px;
    color: #fff;
    transition: 0.3s;
}
.main_screen 
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    position: relative;
}
  
.main_screen .info 
{
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.mask
{
    width: 100vw;
    height: 100vh;
    position: fixed;
}

.mask::after
{
    content: "";
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: whitesmoke;
    opacity: 0.6;
}

.main_screen .info h1
{
    text-transform: uppercase;
    font-size: 4rem;
}

.home_image
{
    width: 100vw;
    height: 100vh;
    position: fixed;
}

.main_screen .name_text
{
    margin-bottom: 0px;
}

.main_screen .btn_container {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    margin-bottom: 18px;
}

.main_screen .btn1, .btn2 {
    flex: 1 1;
}

.main_screen p
{
    font-size: 1.4rem;
    font-weight: 400;
    text-transform: uppercase;
    padding: 0 0 2rem;
}

.content .btn
{
    margin: 1rem 0rem;
    border-radius: 20px;
}

.contact_container 
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 68px;
    gap: 68px;
}

.contact_container .contact_icon 
{
    margin: 10px;
}

.contact_container .contact_icon:hover
{
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}

.content {
    position: relative;
}
 

