    grid-column: span 1;
    border-radius: 30px;
    border: 1px solid #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.project_card img
{
    width: 100%;
}

.project_card video {
