    cursor: pointer;
}

.project_card .btn:hover
{
    background-color: #fbb034;
    background-image: linear-gradient(315deg, #ffdd00 74%, #fbb034 0%,);
    border-radius: 25px;
    color: #fff;
    transition: 0.3s;
}

.video_container {
    position: relative;
    width: 100%;
    padding-bottom: 56.25%; /* 16:9 aspect ratio (height / width * 100) */
}
  
.video_container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.video_container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
  

