.header 
{
    background: #232526;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 70px;
    z-index: 10;
}

.nav_text
{
    color: white;
    font-weight: 620;
}

.nav_tabs 
{
    display: flex;
}

.nav_tabs li 
{
    padding: 0 1rem;
}

.nav_tabs li a 
{
    font-size: 1.2rem;
    font-weight: 500;
}

.burger 
{
    display: none;
}

@media screen and (max-width: 1040px)
{
    .nav_tabs
    {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: fixed;
        top: 50%;
        left: -100%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9);
        z-index: -3;
        transition: 0.2s;
    }

    .nav_tabs.active
    {
       left: 50%;
    }

    .nav_tabs li
    {
        padding: 1rem 0;
    }

    .nav_tabs li a
    {
        font-size: 2rem;
    }

    .burger 
    {
        display: initial;
        cursor: pointer;
    }
}
